import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { Content, ColumnLeft, ColumnRight } from './QuestionsPage';
import Ads from '../components/Ads';
import Pagination from '../components/Pagination';
import QuestionList from '../components/QuestionList';
import Selector from '../components/Selector';
import Line from '../components/Line';
import { questionProps } from '../utilities/proptypes';
import ContentContainer from '../layout/ContentContainer';

const AskForm = styled(ColumnLeft)`
	display: flex;
	flex-direction: column;

	button {
		align-self: end;
	}
`

const options = [
	{ value: 'toan', label: 'Ton' },
	{ value: 'vatLy', label: 'Vt l' },
	{ value: 'hoaHoc', label: 'Ha hc' },
	{ value: 'sinhHoc', label: 'Sinh hc' },
	{ value: 'lichSu', label: 'Lch s' },
	{ value: 'diaLy', label: 'a l' },
];

function AskPage({ hotQuestions, newQuestions }) {
	const history = useHistory();
	const questionTitle = 'Nhp tiu  ca cu hi';
	const questionBody = 'Nhp ni dung ca cu hi';
	const [filter, setFilter] = React.useState(null);

	return (
		<ContentContainer mainTag>
			<h2 className='h2 mt-md'>
				t cu hi mi
				<div className='h2 h2--sub mt-tn'>
					t cu hi  nhn c s h tr t cng ng hc sinh ca
					OnThi247
				</div>
			</h2>
			<Line />
			<Content>
				<AskForm>
					<div className='bold mb-sm'>Tiu  cu hi</div>
					<div className='input-group mb-sm'>
						<input
							type='text'
							className='form-control'
							placeholder={questionTitle}
							aria-label={questionTitle}
						/>
					</div>
					<div className='bold mb-sm'>Ni dung cu hi</div>
					<div className='input-group mb-sm'>
						<textarea
							type='text'
							className='form-control'
							placeholder={questionBody}
							aria-label={questionBody}
							rows={10}
						/>
					</div>
					<div className='bold mb-sm'>Mn hc</div>
					<Selector
						placeholder='Chn mn hc t danh sch'
						value={filter}
						onChange={(selectedValue) => setFilter(() => selectedValue)}
						options={options}
					/>
					<button
						type='button'
						className='btn bold mt-md ask-form__btn'
						onClick={() => history.push('/questions')}
					>
						ng cu hi
					</button>
				</AskForm>
				<ColumnRight>
					<QuestionList
						title='Cu hi ni bt'
						questions={hotQuestions}
						className='mb-md'
					/>
					<QuestionList
						title='Cu hi mi nht'
						questions={newQuestions}
						className='mb-md'
					/>
					<Ads />
				</ColumnRight>
			</Content>
			<Pagination />
		</ContentContainer>
	);
}

AskPage.propTypes = {
	hotQuestions: PropTypes.arrayOf(questionProps).isRequired,
	newQuestions: PropTypes.arrayOf(questionProps).isRequired,
};

const mapStateToProps = (state) => ({
	hotQuestions: Object.values(state.questions.hotQuestions),
	newQuestions: Object.values(state.questions.newQuestions),
});

export default connect(mapStateToProps, null)(AskPage);
