import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { connect } from 'react-redux';
import AnswerCard from '../components/AnswerCard';
import Card from '../components/Card';
import QuestionGrid from '../components/QuestionGrid';
import CommentSection from '../components/CommentSection';
import Ads from '../components/Ads';
import ExamCardList, { Recommend } from '../components/ExamCardList';
import Line from '../components/Line';
import { examProps } from '../utilities/proptypes';
import ContentContainer from '../layout/ContentContainer';
import { appColors, theme } from '../constants';
import congratsImg from '../../public/images/congrats.jpg';

const ResultSummary = styled.div`
	padding-top: 1.5rem;
`;
const SummaryDescription = styled.div`
	background-blend-mode: darken;
	background-image: url(${congratsImg});
	background-size: cover;
	background-position: center;
	color: ${appColors.white};

	height: 18.3rem;
	border-radius: ${theme.borderRound};

	position: relative;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
`;
const SummaryScore = styled.div`
	font-size: 3.6rem;
	font-weight: 600;
	line-height: 1;
`;
const SummaryExp = styled.div`
	margin: 1.6rem 0;
	font-weight: 600;
	font-size: 1.5rem;
`;
const SummaryRank = styled.div`
	text-align: center;
	width: 23rem;
`;

const gutter = '1.4rem';
const Detail = styled.div`
	padding-top: 1.5rem;
`;
const DetailContent = styled.div`
	& > :not(:last-child) {
		margin-right: ${gutter};
	}
	display: flex;
`;
const DetailCol = styled.div`
	display: flex;
	flex-direction: column;

	& > :not(:last-child) {
		margin-bottom: ${gutter};
	}
`;
const DetailCol1 = styled(DetailCol)`
	width: 16rem;
`;
const DetailCol2 = styled(DetailCol)``;
const DetailCol3 = styled(DetailCol)`
	width: 21rem;
`;

const ExamResultPage = ({
	exam,
	nationalExams,
	selectedQuestion,
	timeTaken,
	score,
}) => {
	const { id, title, questionCount, difficulty, questions } = exam;

	return (
		<ContentContainer mainTag>
			<h2 className='h2 mt-md'>
				{title}
				<div className='h2 h2--sub mt-tn'>{`${questionCount} cu hi - Trnh  ${difficulty}`}</div>
			</h2>
			<Line />
			<ResultSummary>
				<h2 className='h2 mb-sm'>Kt qu tng quan</h2>
				<SummaryDescription>
					<SummaryScore>{`${score}/${questions.length}`}</SummaryScore>
					<div>{`${timeTaken.minutes} pht ${timeTaken.seconds} giy`}</div>
					<SummaryExp>+600 exp</SummaryExp>
					<SummaryRank>
						Xp hng 120 trn tng s 360 ngi tham gia lm bi
					</SummaryRank>
				</SummaryDescription>
			</ResultSummary>
			<Detail>
				<h2 className='h2 mb-sm'>p n v li gii chi tit</h2>
				<DetailContent>
					<DetailCol1>
						<Card>
							<h4 className='h4 mb-0'>Cu hi 01</h4>
							<span className='mb-sm'>Cha tr li</span>
							<button type='button' className='btn btn--white'>
								Lu li
							</button>
						</Card>
					</DetailCol1>
					<DetailCol2>
						<AnswerCard
							question={questions[selectedQuestion].question}
							answers={questions[selectedQuestion].answers}
						/>
						<Card>
							<div className='h3'>Li gii chi tit</div>
							<div>
								{questions[selectedQuestion].answerDetail
									.split('\n')
									.map((s) => (
										<p key={s}>{s}</p>
									))}
							</div>
						</Card>
						<CommentSection />
					</DetailCol2>
					<DetailCol3>
						<QuestionGrid questionCount={questions.length} examId={id} />
						<Ads />
					</DetailCol3>
				</DetailContent>
			</Detail>
			<Recommend>
				<ExamCardList exams={nationalExams} title='Bn c th quan tm' />
				<div className='mb-md' />
				<ExamCardList exams={nationalExams} title='Cc bi tp ni bt' />
				<div className='mb-md' />
				<ExamCardList exams={nationalExams} title='Cc bi tp mi nht' />
			</Recommend>
		</ContentContainer>
	);
};

ExamResultPage.propTypes = {
	selectedQuestion: PropTypes.number.isRequired,
	exam: examProps.isRequired,
	nationalExams: PropTypes.arrayOf(examProps).isRequired,
	timeTaken: PropTypes.shape({
		minutes: PropTypes.number,
		seconds: PropTypes.number,
	}).isRequired,
	score: PropTypes.number.isRequired,
};

const mapStateToProps = (state, props) => ({
	selectedQuestion: state.examResult.selectedQuestion,
	exam: state.exams.chemistry[props.match.params.id],
	nationalExams: Object.values(state.exams.national),
	timeTaken: state.examResult.timeTaken,
	score: state.examResult.score,
});

export default connect(mapStateToProps, null)(ExamResultPage);
